.focus\:outline-none:focus { outline: none; }
// outline-light
.focus\:outline-light { outline: 12px solid transparent; outline-offset: 2px; }
.focus\:outline-light:focus { outline: 2px solid var(--color-blue-2); }
.js-focus-visible .focus\:outline-light:not(.focus-visible):focus { outline-color: transparent; }
// outline-dark
.focus\:outline-dark { outline: 12px solid transparent; outline-offset: 2px; }
.focus\:outline-dark:focus { outline: 2px solid var(--color-gray-7); }
.js-focus-visible .focus\:outline-dark:not(.focus-visible):focus { outline-color: transparent; }
// spread & border
.focus\:spread { box-shadow: 0 0 0 12px transparent; }
.focus\:spread:focus { box-shadow: 0 0 0 2px var(--color-blue-1); }
.focus\:border:focus { border-color: var(--color-blue-4); }
