// opacity
.opacity-100 { opacity: 1; }
.opacity-90 { opacity: .9; }
.opacity-80 { opacity: .8; }
.opacity-70 { opacity: .7; }
.opacity-60 { opacity: .6; }
.opacity-50 { opacity: .5; }
.opacity-40 { opacity: .4; }
.opacity-30 { opacity: .3; }
.opacity-20 { opacity: .2; }
.opacity-10 { opacity: .1; }
.opacity-0 { opacity: 0; }
// box-shadow
.shadow-1-light { box-shadow: var(--shadow-1-light); } // light button
.shadow-1-dark { box-shadow: var(--shadow-1-dark); } // colored button
.shadow-8 { box-shadow: var(--shadow-8); } // popover, menu, toast
.shadow-12 { box-shadow: var(--shadow-12); } // modal
// pointer-events
.pointer-events-none { pointer-events: none; }
.pointer-events-auto { pointer-events: all; }
// cursor
.cursor-pointer { cursor: pointer; }
.cursor-help { cursor: help; }
.cursor-crosshair { cursor: crosshair; }
.cursor-not-allowed { cursor: not-allowed; }
