/* font-family */
.font-sans { font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Ubuntu", "Roboto", "Segoe UI", sans-serif; }
.font-mono { font-family: "SFMono-Regular", Menlo, "Ubuntu Mono", "Liberation Mono", Consolas, Courier, monospace; }
/* font-size */
.text-35 { font-size: 35px; }
.text-30 { font-size: 30px; }
.text-26 { font-size: 26px; }
.text-23 { font-size: 23px; }
.text-20 { font-size: 20px; }
.text-17 { font-size: 17px; }
.text-15 { font-size: 15px; }
.text-13 { font-size: 13px; }
.text-11 { font-size: 11px; }
.text-10 { font-size: 10px; }
.text-9 { font-size: 9px; }
/* font-weight */
.font-normal { font-weight: 400; }
.font-medium { font-weight: 500; }
.font-semi-bold { font-weight: 600; }
.font-bold { font-weight: 700; }
/* line-height */
.leading-16 { line-height: 16px; }
.leading-20 { line-height: 20px; }
.leading-24 { line-height: 24px; }
.leading-28 { line-height: 28px; }
.leading-32 { line-height: 32px; }
.leading-40 { line-height: 40px; }
.leading-44 { line-height: 44px; }
.leading-r1p5 { line-height: 1.5; }
/* font-style */
.italic { font-style: italic; }
/* text-transform */
.capitalize { text-transform: capitalize; }
.uppercase { text-transform: uppercase; }
.normal-case { text-transform: none; }
/* text-decoration */
.hover\:text-underline:hover { text-decoration: underline; }
.hover\:text-none:hover { text-decoration: none; }
/* text-align */
.text-left { text-align: left; }
.text-right { text-align: right; }
.text-center { text-align: center; }
.text-justify { text-align: justify; }
/* vertical-align */
.align-baseline { vertical-align: baseline; }
.align-middle { vertical-align: middle; }
.align-top { vertical-align: top; }
.align-bottom { vertical-align: bottom; }
.align-inherit { vertical-align: inherit; }
/* white-space */
.whitespace-normal { white-space: normal; }
.whitespace-no-wrap { white-space: nowrap; }
.whitespace-pre { white-space: pre; }
.whitespace-pre-line { white-space: pre-line; }
.whitespace-pre-wrap { white-space: pre-wrap; }
/* word-wrap */
.break-words { word-wrap: break-word; }
/* wrap */
.truncate { overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }
